<template>
    <swiper class="scroll-prolist" :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide class="spike-swiper-slide" v-for="item in prolist" :key="item">
            <a v-href="{sUrl:item.url,preview}"></a>
            <img :src="item.img" alt="">
            <h4 v-if="bTitle">{{ item.title }}</h4>
            <span class="price">
                {{ item.price }}
            </span>
            <del>
                {{ item.marketPrice }}
            </del>
        </swiper-slide>
    </swiper>
</template>

<script>
import {
    swiper,
    swiperSlide
} from 'vue-awesome-swiper'


export default {
    name: "scroll-prolist",
    props: {
        prolist: {},
        scrollNumber: {
            default: 3.2
        },
        bTitle: {},
        preview:{}
    },
    components: {
        swiper,
        swiperSlide,
    },
    data() {
        return {
            swiperOption: {
                notNextTick:true,
                slidesPerView: this.scrollNumber,
                watchSlidesVisibility: true,
            },
        }
    },
    created() {
        
    },
    computed: {
       
    }
}

</script>

<style lang="scss" scoped>
@import '../../assets/style/config';
@import '../../assets/style/mixins/common';
.scroll-prolist {
    background: #fff;
}

.spike-swiper-slide {
    min-height: 4rem;
}

.spike-swiper-slide .price,
.spike-swiper-slide del {
    text-align: center;
    display: block;
}


.scroll-prolist {
    padding: 0 .4rem;
}

.spike-swiper {
    margin-top: -1px;
    border-top: 1px solid $body-background;
}

.spike-swiper-slide {
    text-align: center;
    padding: 1rem .4rem;
    a{
        @include urlAbsolute();
    }
}

.spike-swiper-slide:last-child {
    padding-right: .4rem;
}

.spike-swiper-slide img {
    display: block;
    width: 100%;
}

.spike-swiper-slide h4 {
    font-size: 1.4rem;
    margin-top: .2rem;
    font-weight: 400;
    white-space: nowrap;
    @include ellipses();
}

.spike-swiper-slide .price {
    color: $color;
    font-size: 1.4rem;
}

.spike-swiper-slide del {
    color: $subsidiary-color;
    font-size: 1.1rem;
}
</style>


